.mainBlock {
  position: relative;
  padding: 0 50px;
  overflow: hidden;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);

  .mainBlockBg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .rotateBg {
    position: absolute;
    left: -220px;
    top: 0;
    width: 62%;
    height: 100%;
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
    box-shadow: 2px 0 12px rgba(0, 0, 0, 0.4);

    svg {
      transform: skew(21deg, 0deg);
    }
  }

  .mainContainer {
    position: relative;
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;

    .textAndIcon {
      position: relative;
      width: calc(100% - 680px);

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 650px;
        height: 650px;
      }

      h1 {
        color: #29323c;
        font-size: 140px;
        font-weight: 400;
        line-height: 1.15;
        margin-bottom: 20px;
      }

      p {
        padding-left: 5px;
        font-size: 32px;
        margin-bottom: 0;
      }

      .text {
        position: relative;
      }
    }
    .photo {
      width: 600px;
      min-height: 100vh;
      display: flex;
      align-items: flex-end;
      padding-top: 90px;
      filter: grayscale(.3)
    }
  }
}
@media screen and (max-width: 1250px) {
  .mainBlock {
    .rotateBg {
      width: 70%;
    }
    .mainContainer {
      .textAndIcon {
        width: calc(100% - 500px);
        h1 {
          font-size: 125px;
        }
      }
      .photo {
        min-height: auto;
        width: 500px;
        img {
          margin-bottom: -12%;
        }
      }
    }
  }
}
@media screen and (max-width: 1020px) {
  .mainBlock {
    padding: 0 30px;
    .rotateBg {
      width: 75%;
    }
    .mainContainer {
      .textAndIcon {
        width: calc(100% - 400px);
        h1 {
          font-size: 100px;
        }
        p {
          font-size: 28px;
        }
      }
      .photo {
        min-height: auto;
        width: 400px;
        img {
          margin-bottom: -12%;
        }
      }
    }
  }
}
@media screen and (max-width: 800px) {
  .mainBlock {
    .rotateBg {
      width: 155%;
      height: 100%;
      left: -85%;
      box-shadow: none;
    }
    .mainContainer {
      display: block;
      .textAndIcon {
        width: 100%;
        padding-top: 70px;
        h1 {
          font-size: 80px;
          text-align: center;
          margin-bottom: 12px;
        }
        p {
          text-align: center;
        }
        svg {
          max-width: 100%;
          max-height: 250%;
          top: 60px;
          left: 0;
          transform: translate(0, 0);
        }
      }
      .photo {
        width: 100%;
        padding-top: 15px;
        img {
          max-width: 70%;
          margin: 0 auto -11% auto;
        }
      }
    }
  }
}
@media screen and (max-width: 620px) {
  .mainBlock {
    padding: 0 15px;
  }
  .mainBlock .mainContainer .textAndIcon h1 {
    font-size: 60px;
  }
  .mainBlock .mainContainer .textAndIcon p {
    font-size: 24px;
  }
}
@media screen and (max-width: 480px) {
  .mainBlock .mainContainer .textAndIcon h1 {
    font-size: 45px;
  }
  .mainBlock .mainContainer .textAndIcon p {
    font-size: 20px;
  }
}